
.navbar {
    background: #5133a4;
    min-height: 70px;
    position: fixed;
    width: 100%;
    -webkit-box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: black;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
    z-index: 99;
    white-space: nowrap;
    margin-left: -26px;
    margin-right: -26px;
}

.navbar li {
    display: inline-block;
    padding: 26px 200px;
    width: 33.3%;
    text-align: center;
}

.navbar a {
    text-decoration: none;
    color: #fff;
    font-weight: 900;
    font-size: 24px;
}

.navbar a {
    text-decoration: none;
    color: #fff;
    font-weight: 900;
}

.navbar-brand {
  padding-left: 10%  
}
